.toggle-menu {
  background-color: #32439d;
  top: 9px;
  right: 20px;
  width: 44px;
  height: 44px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 11px 2px 11px 2px;
  border-radius: 50%;
  cursor: pointer;
}

.line {
  width: 21px;
  height: 2px;
  margin-left: 25%;
  border-radius: 3px;
  background-color: #ffffff;
  transition: transform 0.2s ease-out;
}

.toggle .line1 {
  background-color: #000000;
  transform: scale(0.9) rotateZ(-45deg) translate(-5px, 4px);
}

.toggle .line2 {
  display: none;
}

.toggle .line3 {
  background-color: #000000;
  transform: scale(0.9) rotateZ(45deg) translate(-5px, -4px);
}

.toggle .toggle-menu {
  background-color: #ffffff;
}

.nav__bar {
  display: flex;
