.header {
  width: 100%;
  margin: 0 auto;

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 11px 12px 4px;
    box-shadow: 0 4px 0  rgba(0, 0, 0, 0.25);
  }

  &__logo {
    padding-right: 84px;
  }
}

.big-logo-hidden {
  display: none;
}

.toggle-menu {
  background-color: $blue;
  top: 9px;
  right: 20px;
  width: 44px;
  height: 44px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 11px 2px 11px 2px;
  border-radius: 50%;
  cursor: pointer;
}

.line {
  width: 21px;
  height: 2px;
  margin-left: 25%;
  border-radius: 3px;
  background-color: $white;
  transition: transform 0.2s ease-out;
}

.toggle .line1 {
  background-color: $black;
  transform: scale(0.9) rotateZ(-45deg) translate(-5px, 4px);
}

.toggle .line2 {
  display: none;
}

.toggle .line3 {
  background-color: $black;
  transform: scale(0.9) rotateZ(45deg) translate(-5px, -4px);
}

.toggle .toggle-menu {
  background-color: $white;
}

.nav__bar {
  display: flex;
  align-items: center;
  transition: position 2.9s;
}

.nav__bar-btn {
  position: relative;
  border: none;
  background-color: $white;
  box-shadow: none;
}

.nav__list {
  display: none;
  padding: 0;
  position: absolute;
  margin: 0 auto;
  top: 100%;
  left: 0;
  right: 0;
  background-color: $white;
  list-style: none;
  z-index: 99;
}

.active {
  display: block;
}

.nav__item {
  text-align: center;
  padding: 8px 0;
}

.nav__name {
  color: $blue;
  font-size: 22px;
  text-decoration: none;
  padding-bottom: 4px;
}

.nav__name::before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: $white;
  transform: scaleX(0);
  transition: transform 0.4s ease-in-out;
  transform-origin: left;
}

.nav__name:hover::before {
  transform: scaleX(1);
}

.nav__filter {
  flex-direction: column;
  font-size: 14px;
  line-height: 17px;
  color: $grey-grey;
  padding-right: 28px;
  cursor: pointer;
  padding-left: 28px;
}

.categories {
  display: flex;
  justify-content: center;
  padding-top: 15px;

  &__item {
    color: $dark-blue;
  }
}

.categories a {
  text-decoration: none;
  color: $dark-blue;
}

.categories__item:not(:last-child) {
  padding-bottom: 15px;
}
